import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import axios from 'axios-jsonp-pro'
import { Button, Div, View, PanelHeaderBack, File, FormLayout, Header, Select, PanelHeader, Panel, Input, Banner, Alert, ScreenSpinner, Snackbar, Avatar, Gallery, Title, CardGrid, Card, Text } from '@vkontakte/vkui';
import Icon24Camera from '@vkontakte/icons/dist/24/camera';
import Icon24Linked from '@vkontakte/icons/dist/24/linked';
import Icon24Document from '@vkontakte/icons/dist/24/document';
import Icon16Done from '@vkontakte/icons/dist/16/done';
import Icon16Cancel from '@vkontakte/icons/dist/16/cancel';
import gallery from '../img/gallery.png';
import url from '../img/url.png';
import vkD from '../img/vkD.png';
import ava from '../img/ava.png'
import intro1 from '../img/intro1.png'
import intro2 from '../img/intro2.png'

const images = { heic: require('../img/heic.png'), jpeg: require('../img/jpeg.png'), png: require('../img/png.png'), gif: require('../img/gif.png'), pdf: require('../img/pdf.png'), hdr: require('../img/hdr.png'), svg: require('../img/svg.png') }

export default class Home extends React.Component {
	constructor(props) {
		super(props);
	
		this.state = {
		  activePanel: 'home',
		  history: ['home'],
		  file: {},
		  value: '',
		  activeValue: '',
		  link: '',
		  fileName: '',
		  userLink: '',
		  snackbar: null,
		  vk_platform: 'browser',
		  slideIndex: 0,
		  ownSetting: false,
		  VKPhotos: [],
		  height: '',
		  width: ''
		}
	  }

	  goBack = () => {
		var history = this.state.history;
		
		if ( history.length > 1 ) {
		  history.pop()
		  if (history[history.length - 1] === 'home') {
			document.documentElement.style.setProperty('--showTabbar', 'flex');
			document.documentElement.style.setProperty('--padding', 'calc(var(--safe-area-inset-bottom) + var(--tabbar_height))');
			document.documentElement.style.setProperty('--bottom', 'calc(var(--tabbar_height) + var(--safe-area-inset-bottom))');
			this.closePopout()
		  }
		  console.log(history)
		  if (['VKPhotos', 'home'].includes(history[history.length - 1])) {
			  this.setState({ file: {}, value: '', fileName: '', activeValue: '', width: '', height: '' })
		  }
		  this.setState({ activePanel: history[history.length - 1] })
		}
	  }
	  goToPage( name ) {
		window.history.pushState( { panel: name }, name );
		this.setState({ 
			activePanel: name,
			history: [...this.state.history, name]
		})
	  }

	  openSuccess () {
		this.setState({ snackbar:
		  <Snackbar
			layout="vertical"
			onClose={() => this.setState({ snackbar: null })}
			before={<Avatar size={24} style={{ backgroundColor: 'var(--accent)' }}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
		  >
			Отправили!
		  </Snackbar>
		});
	  }
	  openError () {
		this.setState({ snackbar:
		  <Snackbar
			layout="vertical"
			onClose={() => this.setState({ snackbar: null })}
			before={<Avatar size={24} style={{ backgroundImage: 'linear-gradient(135deg, #fc7373, #cb5e5a)' }}><Icon16Cancel fill="#fff" width={14} height={14} /></Avatar>}
		  >
			Ошибка
		  </Snackbar>
		});
	  }
	  openDoesntLink () {
		this.setState({ snackbar:
		  <Snackbar
			layout="vertical"
			onClose={() => this.setState({ snackbar: null })}
			before={<Avatar size={24} style={{ backgroundImage: 'linear-gradient(135deg, #EC9F05, #FF4E00)' }}><Icon16Cancel fill="#fff" width={14} height={14} /></Avatar>}
		  >
			Не удалось получить изображение
		  </Snackbar>
		});
	  }
	  openBigSize () {
		this.setState({ snackbar:
		  <Snackbar
			layout="vertical"
			onClose={() => this.setState({ snackbar: null })}
			before={<Avatar size={24} style={{ backgroundImage: 'linear-gradient(135deg, #fc7373, #cb5e5a)' }}><Icon16Cancel fill="#fff" width={14} height={14} /></Avatar>}
		  >
			Размер файла превышает 40 МБ
		  </Snackbar>
		});
	  }

	  componentDidMount() {
		window.addEventListener('popstate', () => this.goBack());
		if (window.location.search) {
			var vk_platform = window.location.search.match(/vk_platform=([a-z_]+)/)
			this.setState({vk_platform: vk_platform[1].toString()})
		}
		console.log(1)
		bridge.send("VKWebAppStorageGet", {"keys": ["greeting"]})
		.catch((e)=>console.log(e))
		.then((haveshown)=>{
			//bridge.send("VKWebAppStorageSet", {"key": "greeting", "value": ''})
			if (haveshown?.keys[0]?.value === '') {
				this.setState({activePanel: 'greeting'})
				document.documentElement.style.setProperty('--showTabbar', 'none');
				document.documentElement.style.setProperty('--padding', '0');
			}
		})
	  }

	  download = async () => {
		this.closePopout()
		if (this.state.vk_platform !== 'browser') {
			var key = await bridge.send("VKWebAppStorageGet", {"keys": ["transfers", "size"]});
		 bridge.send("VKWebAppStorageSet", {"key": "transfers", "value": `${Number(key?.keys[0].value) + 1}`});
		 bridge.send("VKWebAppStorageSet", {"key": "size", "value": `${Number(key?.keys[1].value) + this.state.file.size}`});
	   }
			if (['mobile_iphone', 'mobile_iphone_messenger'].includes(this.state.vk_platform)) {
				bridge.send("VKWebAppDownloadFile", {"url": this.state.link, "filename": this.state.fileName + '.' + this.state.value})
				.then(() => this.openSuccess())
				.catch(async ()=>{
					var notifications = await bridge.send("VKWebAppStorageGet", {"keys": ["notifications"]});
					console.log(typeof notifications?.keys[0]?.value)
					notifications.keys[0].value === 'true' ? this.openSuccess() : this.openError();
				});
				bridge.send("VKWebAppTapticNotificationOccurred", {"type": "error"});
			}
			else if (['mobile_android', 'mobile_android_messenger'].includes(this.state.vk_platform)) {
				this.openSuccess()
			}
			else {
				const a = document.createElement('a');
				a.href = await this.state.link;
				a.download = this.state.fileName + '.' + this.state.value;
				a.target = '_blank';
				await a.click()
				this.openSuccess()
			}
	}
	  
	  async exportPhoto () {
		  this.props.setPopout(<ScreenSpinner />)

		const url = `https://converter.dan0102dan.ru/${this.state.value}`;
		const formData = new FormData();
		formData.append('file', this.state.file)
		var notifications, sign;
		if (window.location.search) {
			sign = window.location.search.slice(1)
			notifications = await bridge.send("VKWebAppStorageGet", {"keys": ["notifications"]}).catch(()=>notifications = {keys: [{ value: false }]});
			if (notifications?.keys[0].value === '') {
				notifications = {keys: [{ value: false }]}
			}
		} else {
			sign = 'null'
			notifications = {keys: [{ value: false }]}
		}
		formData.append('sign', sign)
		formData.append('fileName', this.state.fileName)
		formData.append('notifications', notifications?.keys[0].value)
		formData.append('size', [this.state.height.length !== 0 ? this.state.height : null, this.state.width.length !== 0 ? this.state.width : null])
		const config = {
		  responseType: 'blob',
		  headers: {
			'content-type': 'multipart/form-data'
		  }
		}
		const mode = 'cors'
		try {
		  await axios.post(url, formData, config, mode)
            .then(async (response) => {
				console.log(response.data)
			if (response.data.type === 'application/json') {
				console.log('authorization error')
				this.closePopout()
				this.goBack()
				return;
			}
			if (response.data.type === 'text/html') {
				this.closePopout()
				this.goBack()
				this.setState({ file: {}, value: '', fileName: '', activeValue: '' })
				this.openError()
				return;
			}
			var reader = new FileReader();
			reader.onloadend = async () => {
			  this.setState({link: reader.result}, this.download);
			}
			 await reader.readAsDataURL(response.data);
			})
		} catch {
			this.props.setPopout(
				<Alert
				  actionsLayout="vertical"
				  actions={[{
					title: 'Ok',
					autoclose: true,
					mode: 'cancel'
				  }]}
				  onClose={this.closePopout}
				>
				  <h2>Плохое интернет подключение</h2>
				  <p>Проверьте скорость Вашего соединения; в противном случае, перезапустите сервис.</p>
				</Alert>
			  )
		  }
		}

	  closePopout = () => {
		this.props.setPopout(null);
	}

render () {
	return (
	<Panel id={this.id}>
	 <View activePanel={this.state.activePanel} history={this.state.history} onSwipeBack={this.goBack}>
	 	<Panel id="home">
          <PanelHeader>Главная</PanelHeader>
		  <Header>Где находится Ваш файл?</Header>
		  <Banner
        mode="image"
        size="m"
        header="В Фото"
        subheader={<span>Откройте галерею<br />и выберите фото</span>}
        background={
          <div
            style={{
              backgroundColor: '#1e065f',
              backgroundImage: `url(${gallery})`,
              backgroundPosition: 'right center',
              backgroundSize: '95%',
			  backgroundRepeat: 'no-repeat',
            }}
          />
        }
        actions={<File style={{cursor: 'pointer'}} mode="overlay_primary" accept="image/*" before={<Icon24Camera />} controlSize="l"
		onChange={ (e) => {
		  console.log(e.target.files[0])
		if (e.target.files[0]?.size > 40000000) {
			this.openBigSize()
			return;
		}
		  try {
		  if (e.target.files[0].type === 'image/png') {
			  this.setState({ activeValue: 'png', file: e.target.files[0], fileName: e.target.files[0].name.slice(0, -4) })
			  this.goToPage('export')
			  document.documentElement.style.setProperty('--showTabbar', 'none');
			  document.documentElement.style.setProperty('--padding', '0');
			  document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
		  } 
		  else if (e.target.files[0].type === 'image/heic') {
			  this.setState({ activeValue: 'heic', file: e.target.files[0], fileName: e.target.files[0].name.slice(0, -5) })
			  this.goToPage('export')
			  document.documentElement.style.setProperty('--showTabbar', 'none');
			  document.documentElement.style.setProperty('--padding', '0');
			  document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
		  } 
		  else if (e.target.files[0].type === 'image/jpeg') {
			  var name = e.target.files[0].name
			  if (name.match(/\.jpeg$/)) name = name.slice(0, -5)
			  else name = name.slice(0, -4)

			  this.setState({ activeValue: 'jpeg', file: e.target.files[0], fileName: name })
			  this.goToPage('export')
			  document.documentElement.style.setProperty('--showTabbar', 'none');
			  document.documentElement.style.setProperty('--padding', '0');
			  document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
		  }
		  else if (e.target.files[0].type === 'image/gif') {
			this.setState({ activeValue: 'gif', file: e.target.files[0], fileName: e.target.files[0].name.slice(0, -4) })
			this.goToPage('export')
			document.documentElement.style.setProperty('--showTabbar', 'none');
			document.documentElement.style.setProperty('--padding', '0');
			document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
		  }
		  else if (e.target.files[0].type === 'image/pdf') {
			this.setState({ activeValue: 'pdf', file: e.target.files[0], fileName: e.target.files[0].name.slice(0, -4) })
			this.goToPage('export')
			document.documentElement.style.setProperty('--showTabbar', 'none');
			document.documentElement.style.setProperty('--padding', '0');
			document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
		  }
		  else if (e.target.files[0].type === 'image/hdr') {
			this.setState({ activeValue: 'hdr', file: e.target.files[0], fileName: e.target.files[0].name.slice(0, -4) })
			this.goToPage('export')
			document.documentElement.style.setProperty('--showTabbar', 'none');
			document.documentElement.style.setProperty('--padding', '0');
			document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
		  }
		  else if (e.target.files[0].type === 'image/svg+xml') {
			this.setState({ activeValue: 'svg', file: e.target.files[0], fileName: e.target.files[0].name.slice(0, -4) })
			this.goToPage('export')
			document.documentElement.style.setProperty('--showTabbar', 'none');
			document.documentElement.style.setProperty('--padding', '0');
			document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
		  }
		  else {
			this.closePopout()
			this.openDoesntLink()
		  }
		  e.target.value = null;
		 } catch {
			this.openError()
			this.setState({ file: {}, value: '', fileName: '', activeValue: '' })
			this.goBack()
		 }
		}}
		>
		   {['browser', 'desktop_web', 'mobile_web'].includes(this.state.vk_platform) ? 'Выбрать фото' : 'Открыть галерею'}
		</File>}
      />
		  <Banner
        	mode="image"
        	size="m"
        	header="В интернете"
        	subheader={<span>Скопируйте ссылку<br />и вставьте в окно</span>}
        	background={
          	  <div
               style={{
              	backgroundColor: '#56005f',
              	backgroundImage: `url(${url})`,
              	backgroundPosition: 'right center',
              	backgroundSize: '95%',
			  	backgroundRepeat: 'no-repeat',
               }}
		  />
		}
        actions={<Button style={{backgroundColor: '#fff', color: '#000', cursor: 'pointer'}} before={<Icon24Linked />} size="l" onClick={() => {
			this.props.setPopout(
				<Alert
				  onSwipeBack={this.props.setPopout(null)}
				  actions={[{
					title: 'Отмена',
					autoclose: true,
					mode: 'cancel'
				  }, {
					title: 'Далее',
					autoclose: true,
					action: async () => {
					this.props.setPopout(<ScreenSpinner />)
					var blob;
				try {
					 await fetch(`https://cors-anywhere.herokuapp.com/${this.state.userLink}`).then(async (response) => {
						blob = await response.blob()
						function makeid(length) {
							var result           = '';
							var characters       = 'abcdefghiklmnopqrstuvwxyz';
							var charactersLength = characters.length;
							for ( var i = 0; i < length; i++ ) {
							   result += characters.charAt(Math.floor(Math.random() * charactersLength));
							}
							return result;
						 }
						blob.name = makeid(10);
						blob.lastModified = Date.now();
						blob.webkitRelativePath = '';
						})
					} catch {
						this.props.setPopout(
							<Alert
							  actionsLayout="vertical"
							  actions={[{
								title: 'Ok',
								autoclose: true,
								mode: 'cancel'
							  }]}
							  onClose={this.closePopout}
							>
							  <h2>Плохое интернет подключение</h2>
							  <p>Проверьте скорость Вашего соединения; противном случае, перезапустите сервис.</p>
							</Alert>
						  )
					}
					this.closePopout()
					if (blob?.size > 40000000) {
						this.openBigSize()
						return;
					}
					  try {
						if (blob.type === 'image/png') {
							this.setState({ activeValue: 'png', file: blob, fileName: blob.name.slice(0, -4) })
							this.goToPage('export')
							document.documentElement.style.setProperty('--showTabbar', 'none');
							document.documentElement.style.setProperty('--padding', '0');
							document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
						}
						else if (blob.type === 'image/heic') {
							this.setState({ activeValue: 'heic', file: blob, fileName: blob.name.slice(0, -5) })
							this.goToPage('export')
							document.documentElement.style.setProperty('--showTabbar', 'none');
							document.documentElement.style.setProperty('--padding', '0');
							document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
						}
						else if (blob.type === 'image/jpeg') {
							this.setState({ activeValue: 'jpeg', file: blob, fileName: blob.name.slice(0, -4) })
							this.goToPage('export')
							document.documentElement.style.setProperty('--showTabbar', 'none');
							document.documentElement.style.setProperty('--padding', '0');
							document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
						}
						else if (blob.type === 'image/gif') {
						  this.setState({ activeValue: 'gif', file: blob, fileName: blob.name.slice(0, -4) })
						  this.goToPage('export')
						  	document.documentElement.style.setProperty('--showTabbar', 'none');
							document.documentElement.style.setProperty('--padding', '0');
							document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
						  }
						else if (blob.type === 'image/pdf') {
						  this.setState({ activeValue: 'pdf', file: blob, fileName: blob.name.slice(0, -4) })
						  this.goToPage('export')
						  	document.documentElement.style.setProperty('--showTabbar', 'none');
							document.documentElement.style.setProperty('--padding', '0');
							document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
					  	}
						else if (blob.type === 'image/hdr') {
							this.setState({ activeValue: 'hdr', file: blob, fileName: blob.name.slice(0, -4) })
							this.goToPage('export')
							document.documentElement.style.setProperty('--showTabbar', 'none');
							document.documentElement.style.setProperty('--padding', '0');
							document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
						}
						else if (blob.type === 'image/svg+xml') {
							this.setState({ activeValue: 'svg', file: blob, fileName: blob.name.slice(0, -4) })
							this.goToPage('export')
							document.documentElement.style.setProperty('--showTabbar', 'none');
							document.documentElement.style.setProperty('--padding', '0');
							document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
						}
					  	else {
						  this.closePopout()
						  this.openDoesntLink()
						}
						blob = null;
					} catch {
						this.openError()
						this.setState({ file: {}, value: '', fileName: '', activeValue: '' })
						this.goBack()
					}
				   }
				  }]}
				  onClose={this.closePopout}
				>
				  <h2>Введите ссылку</h2>
				  <p>Должна быть ссылка на файл, где не требуется ввести логин/пароль</p>
				  <Input style={{marginTop: 16, marginBottom: -7}} onChange={(e)=> { this.setState({ userLink: e.target.value }) }} type="text" />
				</Alert>
			  )
		}}>Вставить ссылку</Button>}
      />
	  <Banner
        mode="image"
        size="m"
        header="В документах VK"
        subheader={<span>Дайте доступ к данным<br />и выберите фото</span>}
        background={
          <div
            style={{
              backgroundColor: '#384aa0',
              backgroundImage: `url(${vkD})`,
              backgroundPosition: 'right center',
              backgroundSize: '100%',
			  backgroundRepeat: 'no-repeat',
            }}
          />
        }
        actions={<Button style={{backgroundColor: '#fff', color: '#000', cursor: 'pointer'}} before={<Icon24Document />} size="l" onClick={async() => {
			var token = await bridge.send("VKWebAppGetAuthToken", {"app_id": 7561776, "scope": "docs"}).catch(()=>{
				this.openError()
			});
			console.log(token)
			if (token?.access_token) {
				var photos = await bridge.send("VKWebAppCallAPIMethod", {"method": "docs.get", "params": {"type": "4", "v":"5.122", "access_token":token.access_token}}).catch(()=>{this.openError()});
				console.log(photos)
				photos.response.items.sort((a, b) => a.preview ? - 1 : 0)
				if (photos.response.count > 0) {
					this.setState({VKPhotos: photos.response.items})
					this.goToPage('VKPhotos')
					document.documentElement.style.setProperty('--showTabbar', 'none');
					document.documentElement.style.setProperty('--padding', '0');
					document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
				}
				else this.openDoesntLink()
			}
			else {
				this.openError()
			}
		}}>Выбрать документ</Button>}
      />
	  {this.state.snackbar}
      </Panel>

	  <Panel id="VKPhotos">
		<PanelHeader
		  left={<PanelHeaderBack style={{cursor: 'pointer'}} onClick={ () => {
			  this.goBack();
			  document.documentElement.style.setProperty('--showTabbar', 'flex');
			  document.documentElement.style.setProperty('--padding', 'calc(var(--safe-area-inset-bottom) + var(--tabbar_height))');
			  document.documentElement.style.setProperty('--bottom', 'calc(var(--tabbar_height) + var(--safe-area-inset-bottom))');
			}} />}
		  >
			Фотографии VK
		</PanelHeader>
		  {this.state.VKPhotos ? <CardGrid style={{marginTop: 0, marginBottom: 16}}>
			  {this.state.VKPhotos.map(VKPhotos => <Card
			  style={{marginRight: 4, marginLeft: 0}}
			  size="m"
			  onClick={async (e) => {
				this.props.setPopout(<ScreenSpinner />)
				var BB;
				var a = e.currentTarget.dataset.mode.split('|')
				var b = this.state.VKPhotos.find(e => e.id === Number(a[1]));
					 await fetch(`https://cors-anywhere.herokuapp.com/${b.url}`).then(async (response) => BB = await response.blob())
						.catch(() =>
						  this.props.setPopout(
							<Alert
							  actionsLayout="vertical"
							  actions={[{
								title: 'Ok',
								autoclose: true,
								mode: 'cancel'
							  }]}
							  onClose={this.closePopout}
							>
							  <h2>Плохое интернет подключение</h2>
							  <p>Проверьте скорость Вашего соединения; противном случае, перезапустите сервис.</p>
							</Alert>
						  ));
					this.closePopout()
					if (BB.type === 'application/octet-stream') {
						BB = await new Blob([BB], {type: b.ext === 'svg' ? 'image/svg+xml' : b.ext})
					}
					console.log(b)
					console.log(BB)
					BB.name = b.title;
					BB.lastModified = b.date;
					
					if (BB?.size > 40000000) {
						this.openBigSize()
						return;
					}

					  try {
						if (BB.type === 'image/png') {
							this.setState({ activeValue: 'png', file: BB, fileName: BB.name.slice(0, -4) })
							this.goToPage('export')
							document.documentElement.style.setProperty('--showTabbar', 'none');
							document.documentElement.style.setProperty('--padding', '0');
							document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
						}
						else if (BB.type === 'image/heic') {
							this.setState({ activeValue: 'heic', file: BB, fileName: BB.name.slice(0, -5) })
							this.goToPage('export')
							document.documentElement.style.setProperty('--showTabbar', 'none');
							document.documentElement.style.setProperty('--padding', '0');
							document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
						}
						else if (BB.type === 'image/jpeg') {
							var name = BB.name
			  				if (name.match(/\.jpeg$/)) name = name.slice(0, -5)
			  				else name = name.slice(0, -4)
							this.setState({ activeValue: 'jpeg', file: BB, fileName: name })
							this.goToPage('export')
							document.documentElement.style.setProperty('--showTabbar', 'none');
							document.documentElement.style.setProperty('--padding', '0');
							document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
						}
						else if (BB.type === 'image/gif') {
						  this.setState({ activeValue: 'gif', file: BB, fileName: BB.name.slice(0, -4) })
						  this.goToPage('export')
						  	document.documentElement.style.setProperty('--showTabbar', 'none');
							document.documentElement.style.setProperty('--padding', '0');
							document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
						  }
						else if (BB.type === 'image/pdf') {
						  this.setState({ activeValue: 'pdf', file: BB, fileName: BB.name.slice(0, -4) })
						  this.goToPage('export')
						  	document.documentElement.style.setProperty('--showTabbar', 'none');
							document.documentElement.style.setProperty('--padding', '0');
							document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
					  	}
						else if (BB.type === 'image/hdr') {
							this.setState({ activeValue: 'hdr', file: BB, fileName: BB.name.slice(0, -4) })
							this.goToPage('export')
							document.documentElement.style.setProperty('--showTabbar', 'none');
							document.documentElement.style.setProperty('--padding', '0');
							document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
						}
						else if (BB.type === 'image/svg+xml') {
							this.setState({ activeValue: 'svg', file: BB, fileName: BB.name.slice(0, -4) })
							this.goToPage('export')
							document.documentElement.style.setProperty('--showTabbar', 'none');
							document.documentElement.style.setProperty('--padding', '0');
							document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
						}
					  	else {
							this.closePopout()
							this.openDoesntLink()
						}
						BB = null;				
					} catch {
						this.openError()
						this.setState({ file: {}, value: '', fileName: '', activeValue: '' })
						this.goBack()
					}
			  }}
			  data-mode={VKPhotos.owner_id+'|'+VKPhotos.id}
			  key={VKPhotos.id}
			  >
				<Div>
					<Title style={{marginRight: 'auto', marginLeft: 'auto'}} level="3" weight="semibold">{VKPhotos.title}</Title>
					{VKPhotos?.preview?.photo?.sizes[5]?.src ? <img src={VKPhotos.preview.photo.sizes[5].src} alt="" style={{ height: '10vh', width: '100%', objectFit: 'contain' }}></img> : VKPhotos?.preview?.photo?.sizes[4]?.src ? <img src={VKPhotos.preview.photo.sizes[4].src} alt="" style={{ height: '10vh', width: '100%', objectFit: 'contain' }}></img> : VKPhotos?.preview?.photo?.sizes[3]?.src ? <img src={VKPhotos.preview.photo.sizes[3].src} alt="" style={{ height: '10vh', width: '100%', objectFit: 'contain' }}></img> : VKPhotos?.preview?.photo?.sizes[2]?.src ? <img src={VKPhotos.preview.photo.sizes[2].src} alt="" style={{ height: '10vh', width: '100%', objectFit: 'contain' }}></img> : VKPhotos?.preview?.photo?.sizes[1]?.src ? <img src={VKPhotos.preview.photo.sizes[1].src} alt="" style={{ height: '10vh', width: '100%', objectFit: 'contain' }}></img> : VKPhotos?.preview?.photo?.sizes[0]?.src ? <img src={VKPhotos.preview.photo.sizes[0].src} alt="" style={{ height: '10vh', width: '100%', objectFit: 'contain' }}></img> : null}
					<Text style={{marginTop: 3}} weight="regular">Размер изображения: {(VKPhotos.size/1000000).toFixed(3)} МБ</Text>
			  	</Div>
			  </Card>)}
			</CardGrid>
			: <Title style={{marginLeft: 16, marginTop: 16}} level="2" weight="medium">
				Нет данных
			  </Title>}
			{this.state.snackbar}
        </Panel>

        <Panel id="export">
          <PanelHeader
		  left={<PanelHeaderBack style={{cursor: 'pointer'}} onClick={ () => {
			  this.goBack();
			  document.documentElement.style.setProperty('--showTabbar', 'flex');
			  document.documentElement.style.setProperty('--padding', 'calc(var(--safe-area-inset-bottom) + var(--tabbar_height))');
			  document.documentElement.style.setProperty('--bottom', 'calc(var(--tabbar_height) + var(--safe-area-inset-bottom))');
			}} />}
		  >
			Конвертация
		  </PanelHeader>
		
		{this.state.file.name &&
		 <div style={{display:'flex', justifyContent: 'center', width: '100%', height: '22vh', marginTop: 16}}>
		  <img src={URL.createObjectURL(this.state.file)} alt="" style={{ height: '22vh', width: '100%', objectFit: 'contain' }}></img>
		 </div>}

		{this.state.file.name &&
		 <div style={{display:'flex', justifyContent: 'center', marginTop: 16}}>
		  <Header style={{ marginRight: -3 }}>Из</Header>
		   <Input disabled type="text" defaultValue={['image/heic', 'image/jpeg'].includes(this.state.file.type) ? this.state.file.name.match(/\.jpeg$/) ? this.state.file.name.slice(0, -5) : this.state.file.name.slice(0, -4) : this.state.file.name.slice(0, -4)} />
		  <img src={images[this.state.activeValue]} alt="" style={{ marginLeft: 7, height: '44px' }}></img>
		 </div>}

		  <FormLayout>
			 <Select onChange={(e) => {
			this.setState({ value: e.target.value });
			if (['mobile_iphone', 'mobile_iphone_messenger'].includes(this.state.vk_platform)) {
				bridge.send("VKWebAppTapticSelectionChanged", {});
			}
			}} top="Выберите формат, в который хотите преобразовать" placeholder="Выберите формат">
				{this.state.activeValue === 'jpeg' ? null : <option value="jpeg">jpeg</option>}
				{this.state.activeValue === 'png' ? null : <option value="png">png</option>}
				{this.state.activeValue === 'pdf' ? null : <option value="pdf">pdf</option>}
				{this.state.activeValue === 'hdr' ? null : <option value="hdr">hdr</option>}
				{this.state.activeValue === 'gif' ? null : <option value="gif">gif</option>}
      		</Select>
    	  </FormLayout>

		{this.state.value &&
		 <div style={{display:'flex', justifyContent: 'center', marginTop: 4, marginBottom: 4}}>
		  <div style={{widht: '50%', marginLeft: 12, marginRight: 4}}>
        	<Input
			type="number"
			placeholder='Ширина'
			value={this.state.width}
			max={10000}
			status={(this.state.width.length !== 0) ? 'valid' : null}
			onChange={(e)=>{
				console.log(e.target.value)
				if (e.target.value > 10000) e.target.value = 10000;
				if (e.target.value < 0) e.target.value = 0;
				this.setState({width: e.target.value})
			}} />
      	  </div>
		  <div style={{widht: '50%', marginRight: 12, marginLeft: 4}}>
			<Input
			type="number"
			placeholder='Высота'
			value={this.state.height}
			max={10000}
			status={(this.state.height.length !== 0) ? 'valid' : null}
			onChange={(e)=>{
				console.log(e.target.value)
				if (e.target.value > 10000) e.target.value = 10000;
				if (e.target.value < 0) e.target.value = 0;
				this.setState({height: e.target.value})
			}} />
      	  </div>	
		 </div>}

		{this.state.value &&
		 <div style={{display:'flex', justifyContent: 'center', marginTop: 16, marginBottom: 16}}>
		  <Header style={{ marginRight: -3 }}>В</Header>
		   <Input
		   type="text"
		   onChange={(e)=> { console.log(e.target.value); this.setState({ fileName: e.target.value }) }}
		   value={this.state.fileName}
		   status={(this.state.fileName.length !== 0 && this.state.fileName.length < 255) ? 'valid' : 'error'}
		   />
		  <img src={ images[this.state.value] } alt="" style={{ marginLeft: 7, height: '44px' }}></img>
		 </div>}

			{this.state.value && (this.state.fileName.length !== 0 && this.state.fileName.length < 255) &&
				<Div>
				{['mobile_iphone', 'mobile_iphone_messenger'].includes(this.state.vk_platform)
				? <Button mode="commerce" size="xl" onClick={async() => {
					await this.exportPhoto()
				} }>
					Скачать в {this.state.value}
				  </Button>
				: <Button style={{cursor: 'pointer'}} mode="commerce" size="xl" onClick={async() => {
				  if (this.state.vk_platform !== 'browser') {
				   if (['mobile_android', 'mobile_android_messenger'].includes(this.state.vk_platform)) {
					try {
						var mes = await bridge.send("VKWebAppAllowMessagesFromGroup", {"group_id": 151064772});
						if (mes.result === true) {
						  await bridge.send("VKWebAppStorageSet", {"key": "notifications", "value": 'true'});
						}
					} catch {
						this.openError();
						return;
					}
				   }
				  }
					await this.exportPhoto()
				}
				}>
					Скачать в {this.state.value}
				   </Button>}
				</Div>}
			{this.state.snackbar}
        </Panel>

		<Panel id="greeting" style={{ position: 'fixed' }}>
		  <Gallery 
		  		slideWidth="100%"
                align="center"
                style={{ height: '100vh' }}
                slideIndex={this.state.slideIndex}
				onChange={slideIndex => this.setState({slideIndex})}
				bullets="light"
		  >
		  	<div style={{ backgroundColor: '#8818d1', display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }}>
			  	<img className='IntroImg' src={ava} alt="" style={{ width: '300px', height: '300px', marginBottom: 64 }} />
				<Title level="1" weight="heavy" style={{ color: '#fff', marginBottom: 16, fontSize: '2.5em', textAlign: 'center' }}>Конвертер</Title>
				<Title level="3" weight="semibold" style={{ color: '#fff', marginBottom: 4, textAlign: 'center' }}>
					Добро пожаловать в Конвертер!
				</Title>
				<Title level="5" weight="semibold" style={{ color: '#fff', textAlign: 'center', marginBottom: 48, padding: '0 10px' }}>
					Свайпни влево, чтобы узнать подробнее.
				</Title>
				<Div>
				  <Button style={{cursor: 'pointer'}} size='xl' mode='commerce' onClick={()=> {
					  bridge.send("VKWebAppStorageSet", {"key": "greeting", "value": 'true'})
					  this.setState({ activePanel: 'home' })
					  document.documentElement.style.setProperty('--showTabbar', 'flex');
					  document.documentElement.style.setProperty('--padding', 'calc(var(--safe-area-inset-bottom) + var(--tabbar_height))');
					}}>
					Пропустить
				  </Button>
				</Div>
			</div>
			<div style={{ backgroundColor: '#a3c6c7', display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }}>
			  	<img className='IntroImg' src={intro1} alt="" style={{ width: '300px', height: '300px', marginBottom: 64 }} />
				<Title level="1" weight="heavy" style={{ color: '#fff', marginBottom: 16, fontSize: '2.5em', textAlign: 'center' }}>
					Форматы
				</Title>
				<Title level="3" weight="semibold" style={{ color: '#fff', marginBottom: 48, textAlign: 'center', padding: '0 10px' }}>
					Доступны самые популярные расширения, такие как PNG, JPG, SVG и так далее...
				</Title>
				<Div>
				  <Button style={{cursor: 'pointer'}} size='xl' mode='commerce' onClick={()=>this.setState({slideIndex: 2})}>
					Вау
				  </Button>
				</Div>
			</div>
			<div style={{ backgroundColor: '#fe96ce', display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }}>
			  	<img className='IntroImg' src={intro2} alt="" style={{ width: '300px', height: '300px', marginBottom: 64 }} />
				<Title level="1" weight="heavy" style={{ color: '#fff', marginBottom: 16, fontSize: '2.5em', textAlign: 'center' }}>
					Безопасность
				</Title>
				<Title level="3" weight="semibold" style={{ color: '#fff', marginBottom: 48, textAlign: 'center', padding: '0 10px' }}>
					Всё происходит в реальном времени и без сохранения на сторонние ресурсы.
				</Title>
				<Div>
				  <Button style={{cursor: 'pointer'}} size='xl' mode='commerce' onClick={()=> {
					  bridge.send("VKWebAppStorageSet", {"key": "greeting", "value": 'true'})
					  this.setState({ activePanel: 'home' })
					  document.documentElement.style.setProperty('--showTabbar', 'flex');
					  document.documentElement.style.setProperty('--padding', 'calc(var(--safe-area-inset-bottom) + var(--tabbar_height))');
					}}>
					Перейти в сервис!
				  </Button>
				</Div>
			</div>
		  </Gallery>
        </Panel>
      </View>
	</Panel>
	)
 }
}